
body{
	overflow:hidden;
	background-color: #000000;
background-image: url("https://www.transparenttextures.com/patterns/45-degree-fabric-light.png");
}
.game-display-container{
	margin:auto;
	width: 100vmin;
	height: 100vmin;
	position: relative;
	

}
.board-image{
	width: 100vmin;
	margin-top: 20vmin;
	position: relative;
}
.whitechecker{
	background: url('./images/checker-white.png');
	background-size: cover;
}
.blackchecker{
	background: url('./images/checker-black.png');
	background-size: cover;
}
.whiteking{
	background: url('./images/king-white.png');
	background-size: cover;
}
.blackking{
	background: url('./images/king-black.png');
	background-size: cover;
}
.piece{
	position:absolute;
	font-size: 50px;
	z-index: 2;
}
.modal-env{
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.5);
	z-index: 3;

}
.modal-inner-container{
	width: 50%;
	margin-left: 25%;
	margin-top: 15%;
	padding: 10px 0;
	border-radius: 15px;
	background-color: #aaa;
}
.modal-title,
.slider-container{
	width:100%;
}
.modal-title{
	padding: 10px 0;
	font-size: 24px;
	text-align: center;	
}
.slider{
	width: 70%;
	margin-left: 15%;
	margin-right: 15%;	
}
.white-container{
	color:white;
	width: 50%;
	display: inline-block;	
	text-align: center;	
	background-color: 	#333;
	border:2px solid #333;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.black-container{
	color:black;
	width: 50%;
	text-align: center;	
	display: inline-block;	
	border:2px solid #333;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;	
}
.start-container{
	text-align: center;	
	margin:10px 0;
}
.start-button{
	font-size: 20px;
	border:1px solid black;
	padding: 3px 8px;
	display: inline-block;	
}
.start-button:hover{
	background-color: #333;
	color: white;
	cursor: pointer;
}
.gameover{
	background: -webkit-radial-gradient(rgb(200,200,200) 30%, rgba(200,200,200,0) 70%);
	background: -o-radial-gradient(rgb(200,200,200) 30%, rgba(200,200,200,0) 70%);
	background: radial-gradient(rgb(200,200,200) 30%, rgba(200,200,200,0) 70%);
	font-size: 70px;
	text-align: center;	
	line-height: 1000%;
	width: 70%;	
	height: 70%;	
	margin:5% 15%;
}


